import React, { useState, useEffect, useReducer } from "react";
import * as SC from "../styled";

const reducer = (state, action) => {
  switch (action.type) {
    case "UPDATE":
      console.log(action.i, action.field, action.newValue);
      let newState = [...state];
      newState[action.i][action.field] = action.newValue;
      console.log(newState);
      return newState;
    default:
      return state;
  }
};

const Score = (props) => (
  <div
    className={`c`}
    style={{
      display: "flex"
    }}
  >
    {props.isEditing && (
      <div
        style={{ zIndex: 9999 }}
        className={`p m c bdr`}
        onClick={(e) =>
          props.handleUpdate(props.i, "score", props.item.score - 1)
        }
      >
        -
      </div>
    )}
    <div className={`p m c`}>{props.item.score}</div>
    {props.isEditing && (
      <div
        className={`p m c bdr`}
        onClick={(e) =>
          props.handleUpdate(props.i, "score", props.item.score + 1)
        }
      >
        +
      </div>
    )}
  </div>
);

const List = (props) => {
  const [state, dispatch] = useReducer(reducer, props.list);
  const [isEditing, setIsEditing] = useState(false);

  const toggleEditMode = () => {
    setIsEditing(!isEditing);
  };

  const handleUpdate = (i, field, newValue) => {
    dispatch({
      type: "UPDATE",
      i: i,
      field: field,
      newValue: newValue
    });
    props.handleSave(props.listName, state);
  };

  useEffect(() => {}, [state]);

  return (
    <div>
      <div
        className="c"
        style={{
          display: "flex"
        }}
      >
        <b style={{ width: "100%" }}>{String(props.listName).toUpperCase()}</b>
        <SC.ToggleLight toggle={!isEditing} onClick={toggleEditMode}>
          <span role="image">{isEditing ? "💾" : "⚙️"}</span>
        </SC.ToggleLight>
      </div>
      <div className="fwmp">
        {state.length > 0 &&
          state.map((item, i) => (
            <div key={item.name} className={`group-item c`}>
              <b>{item.name}</b>
              {item.score && (
                <Score
                  i={i}
                  item={item}
                  handleUpdate={handleUpdate}
                  isEditing={isEditing}
                />
              )}
            </div>
          ))}
      </div>
    </div>
  );
};

export default List;
